header {
  background: #eee;
  color: #333;
  height: 250px;
  text-align: center;
  padding-top: 80px
}

header h1 {
  margin-top: 0
}

header h4 {
  color: #555
}

footer {
  border-top: 1px solid #eee;
  height: 30px;
  line-height: 30px;
  text-align: center;
  margin-top: 20px;
}

.item {
  position: relative;
  display: block;
  padding: 10px 15px;
  margin-bottom: -1px;
  background-color: #fff;
  border: 1px solid #ddd;
}

.media-left,
.media-right {
  display: table-cell;
  vertical-align: top;
  display: inline-block
}

.media-left {
  padding-right: 10px;
}

.loader {
  padding: 10px;
  text-align: center
}

.github-corner:hover .octo-arm {
  -webkit-animation: octocat-wave 560ms ease-in-out;
  animation: octocat-wave 560ms ease-in-out
}

@-webkit-keyframes octocat-wave {
  0%,
  100% {
    -webkit-transform: rotate(0);
    transform: rotate(0)
  }
  20%,
  60% {
    -webkit-transform: rotate(-25deg);
    transform: rotate(-25deg)
  }
  40%,
  80% {
    -webkit-transform: rotate(10deg);
    transform: rotate(10deg)
  }
}

@keyframes octocat-wave {
  0%,
  100% {
    -webkit-transform: rotate(0);
    transform: rotate(0)
  }
  20%,
  60% {
    -webkit-transform: rotate(-25deg);
    transform: rotate(-25deg)
  }
  40%,
  80% {
    -webkit-transform: rotate(10deg);
    transform: rotate(10deg)
  }
}


